.place__trash {
    background-image: url(../../../images/places-trash.svg);
    position: absolute;
    top: 18px;
    right: 15px;
    width: 18px;
    height: 19.3px;
    cursor: pointer;
    transition: visibility 0.1s, opacity 0.1s linear;
}

.place__trash:hover {
    opacity: 0.6;
}