.header {
    display: flex;
    border-bottom: 1px solid #545454;
    min-height: 74px;
    max-width: 880px;
    margin: 45px auto 40px;
    align-items: flex-start;
    justify-content: space-between;
}

@media screen and (max-width: 1279px) {
    .header {
        margin-bottom: 42px;
    }
}